<template>
  <div class="container">
    <div class="row">
      <div class="col text-left">
        <h2>New book</h2>
        <div class="row">
            <div class="col">
              <b-alert
                :show="dismissCountDown"
                dismissible
                :variant="alert.color"
                @dismissed="dismissCountDown=0"
                @dismiss-count-down="countDownChanged"
              >{{alert.message}}</b-alert>
            </div>
        </div>
        <div class="row">
          <div class="col">
            <b-card
              border-variant="primary"
              :header="book.title"
              header-bg-variant="primary"
              header-text-variant="white"
              align="center"
            >
              <b-card-text>This is the infromation about the book</b-card-text>
              <form @submit="onSubmit">
                <div class="form-group row">
                  <label for="title" class="col-sm-2 col-form-label">Title</label>
                  <div class="col-sm-6">
                    <input
                      type="text"
                      placeholder="Title"
                      name="title"
                      class="form-control"
                      v-model.trim="book.title"
                    >
                  </div>
                </div>
                <div class="form-group row">
                  <label for="description" class="col-sm-2 col-form-label">Description</label>
                  <div class="col-sm-6">
                    <textarea
                      name="description"
                      class="form-control"
                      rows="3"
                      placeholder="Description"
                      v-model.trim="book.description"
                    ></textarea>
                  </div>
                </div>

                <div class="row">
                  <div class="col text-right">
                    <b-button 
                    type="submit" 
                    variant="primary" 
                    pill 
                    :disabled="validaCampos() ? true : false"
                    >Create</b-button>
                    <b-button
                      type="submit"
                      variant="warning"
                      pill
                      class="ml-1"
                      :to="{name: 'ListBook'}"
                    >Back to List</b-button>
                  </div>
                </div>
              </form>
            </b-card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import sweetl from 'sweetalert';
export default {
  data() {
    return {    
      book: {
        title: "",
        description: "",        
      },
      alert:{
          color:'',
          message:''
      },
      dismissSecs: 5,
      dismissCountDown: 0,
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      const path = 'books/';
      axios
        .post(path, this.book) //Book's values
        .then(res => {
          this.book.title = res.data.title;
          this.book.description = res.data.description;
          this.alert.color = "success";
          this.alert.message = "The book was updated successfuly !";
          swal("The book was created succesfuly","","success");
          
        })
        .catch(error => {
          console.log("THERE WAS AN ERROR >:" + error);
          this.alert.color = "danger";
          this.alert.message = "There was an error for creating the book";
        });
        this.showAlert();
    },

    validaCampos() {
      let band = false;
      if (this.book.description == "" || this.book.name == ""){
          return true;
      }
        return band;
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  }
};
</script>