<template>
  <div class="conatiner">
    <div class="row">
      <div class="col text-left">
        <h2>Books List</h2>
        <b-button size="sm" variant="info" pill :to="{name :'NewBook'}" class="m-2">Register a book</b-button>
        <div class="col-md-12">
          <b-table striped hover :items="books" :fields="fields">
            <template v-slot:cell(action)="data">
              <b-button 
              size="sm" 
              variant="primary" 
              class="mr-2" 
              pill 
              :to="{name:'EditBook',params:{bookId:data.item.id}}"
              >
              Edit</b-button>
              <b-button 
              size="sm" 
              variant="danger" 
              pill 
              :to="{name:'DeleteBook',params:{bookId:data.item.id}}"
              >Delete</b-button>
            </template>
          </b-table>
        </div>
      </div>
    </div>
  </div>
</template>         
<script>
export default {
  data() {
    return {
      // Campos que tendra la tabla a pintar
      fields: [
        { key: "title", label: "Título" },
        { key: "description", label: "Descripción" },
        { key: "action", label: "" }
        // {key:'Socred',label:'Ranking'}
      ],
      books: []
    };
  },
  methods: {
    getBooks() {
      this.axios
        .get("books/") //Always puts the / at the end of the path
        .then(res => {
          this.books = res.data;
        })
        .catch(e => {
          console.log(e);
        });
    },
  },
  created() {
    this.getBooks();
  }
};
</script>