<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <h3>Are you sure you wish delete this book?</h3>
        <p>Title : {{this.bookDelete.title}}</p>
        <p>Description : {{this.bookDelete.description}}</p>
      </div>
    </div>
  <!-- BUTTON -->
    <div class="row">
        <div class="col">
            <b-button
            @click="deleteBook" 
            variant="danger"
            >
            Delete Book
            </b-button>
        </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import swal from "sweetalert";
export default {
  data() {
    return { 
      bookId:this.$route.params.bookId,     
      bookDelete: {
        title: "",
        description: ""
      }      
    };
  },
  methods: {
    getBook() {
      const path = `books/${this.bookId}/`;
      axios.get(path)
        .then(res => {
          this.bookDelete.title = res.data.title;
          this.bookDelete.description = res.data.description;
        })
        .catch(error => {
          console.log("THERE WAS AN ERROR >:" + error);
        });
    },

    deleteBook(){
      const path = `books/${this.bookId}/`;
      axios.delete(path)
      .then(res=>{
          location.href="/books"
      })
      .catch(error =>{
          swal("It couldn't delete the book","","error");
          console.log(error);
          
      })
    }
  },
  created() {
      this.getBook();
  },
};
</script>